import { ComponentProps, FC, useMemo } from 'react';
import classNames from 'classnames';

// Types
import { TestMetadata } from 'types';

// Styles
import './NavBarItem.scss';

export interface NavBarItemProps extends ComponentProps<'div'> {
  /** Custom class to pass to nav bar item */
  className?: string;
  /** TestMetadata to use for nar bar item */
  testMetadata?: TestMetadata;
  /** Set visibility on narbaritem */
  visibility?: 'all' | 'desktop' | 'mobile';
}

const NavBarItem: FC<NavBarItemProps> = ({
  className,
  testMetadata,
  children,
  visibility = 'all',
  ...otherProps
}) => {
  const navBarItemClassNames = useMemo(
    () =>
      classNames(
        'sb-navbar-item',
        {
          [`sb-navbar-item__visibility-${visibility}`]: visibility !== 'all',
        },
        className,
      ),
    [className, visibility],
  );

  return (
    <div className={navBarItemClassNames} {...testMetadata} {...otherProps}>
      {children}
    </div>
  );
};

export default NavBarItem;
